</script>
<template>
    <div>
        <div class="toolbar">
            <el-button @click="undo"></el-button>
            <el-button @click="redo"></el-button>
            <label for="input" class="insert">
                
                <input
                    id="input"
                    type="file"
                    hidden
                    @change="handleFileChange"
                />
            </label>
            
            <el-button style="margin-left: 10px;" @click="preview(false)"></el-button>
            <el-button @click="save"></el-button>
            <el-button @click="store"></el-button>
            <el-button @click="readStore"></el-button>
            <el-button @click="sendXMSG"></el-button>
            <el-button @click="clearCanvas"></el-button>
            <el-button :disabled="!areaData.components.length" @click="compose"></el-button>
            <el-button
                :disabled="!curComponent || curComponent.isLock "
                @click="decompose"
            >
                
            </el-button>
            <!--|| curComponent.component != 'Group'-->

            <el-button :disabled="!curComponent || curComponent.isLock" @click="lock"></el-button>
            <el-button :disabled="!curComponent || !curComponent.isLock" @click="unlock"></el-button>
            <el-button @click="preview(true)"></el-button>
            <el-button @click="logout">{{ loginStatus }}</el-button>

            <div class="canvas-config">
                <span></span>
                <input v-model="canvasStyleData.width">
                <span>*</span>
                <input v-model="canvasStyleData.height">
            </div>
            <div class="canvas-config">
                <span></span>
                <input v-model="scale" @input="handleScaleChange"> %
            </div>
            
        </div>

        <!--  -->
        <Preview v-if="isShowPreview" :is-screenshot="isScreenshot" @close="handlePreviewChange" />
    </div>
</template>

<script>
import generateID from '@/utils/generateID'
import toast from '@/utils/toast'
import { mapState } from 'vuex'
import Preview from '@/components/Editor/Preview'
import { commonStyle, commonAttr } from '@/custom-component/component-list'
import eventBus from '@/utils/eventBus'
import { deepCopy, $ } from '@/utils/utils'
import { divide, multiply } from 'mathjs'
import { saveAs } from 'file-saver'

export default {
    components: { Preview },
    data() {
        return {
            isShowPreview: false,
            needToChange: [
                'top',
                'left',
                'width',
                'height',
                'fontSize',
            ],
            scale: '100%',
            timer: null,
            isScreenshot: false,
            loginStatus: '',
        }
    },
    computed: mapState([
        'componentData',
        'canvasStyleData',
        'areaData',
        'curComponent',
        'curComponentIndex',
    ]),
    created() {
        eventBus.$on('preview', this.preview)
        eventBus.$on('save', this.save)
        eventBus.$on('clearCanvas', this.clearCanvas)

        this.scale = this.canvasStyleData.scale
        let user_name = localStorage.getItem('user_name')
        this.loginStatus = user_name ? ''+localStorage.getItem('user_name')+'':''
    },
    methods: {
        format(value) {
            return multiply(value, divide(parseFloat(this.scale), 100))
        },

        getOriginStyle(value) {
            return divide(value, divide(parseFloat(this.canvasStyleData.scale), 100))
        },

        handleScaleChange() {
            clearTimeout(this.timer)
            this.timer = setTimeout(() => {
                //  0
                // eslint-disable-next-line no-bitwise
                this.scale = (~~this.scale) || 1
                const componentData = deepCopy(this.componentData)
                componentData.forEach(component => {
                    Object.keys(component.style).forEach(key => {
                        if (this.needToChange.includes(key)) {
                            if (key === 'fontSize' && component.style[key] === '') return

                            // 
                            //  * 
                            component.style[key] = this.format(this.getOriginStyle(component.style[key]))
                        }
                    })
                })

                this.$store.commit('setComponentData', componentData)
                //  https://github.com/woai3c/visual-drag-demo/issues/74
                this.$store.commit('setCurComponent', { component: componentData[this.curComponentIndex], index: this.curComponentIndex })
                this.$store.commit('setCanvasStyle', {
                    ...this.canvasStyleData,
                    scale: this.scale,
                })
            }, 1000)
        },
        
        lock() {
            this.$store.commit('lock')
        },

        unlock() {
            this.$store.commit('unlock')
        },

        compose() {
            this.$store.commit('compose')
            this.$store.commit('recordSnapshot')
        },

        decompose() {
            this.$store.commit('decompose')
            this.$store.commit('recordSnapshot')
        },

        undo() {
            this.$store.commit('undo')
        },

        redo() {
            this.$store.commit('redo')
        },

        async addImage(img_id,img)
        {
            console.log('addImage:',img_id,img)
            this.$store.commit('addComponent', {
                component: {
                    ...commonAttr,
                    id: generateID(),
                    component: 'Picture',
                    label: '',
                    icon: '',
                    propValue: {
                        url: img_id,
                        flip: {
                            horizontal: false,
                            vertical: false,
                        },
                    },
                    style: {
                        ...commonStyle,
                        top: 0,
                        left: 0,
                        width: img.width,
                        height: img.height,
                    },
                },
            })

            this.$store.commit('recordSnapshot')
        },

        async handleFileChange(e) {
            const file = e.target.files[0]
            if (!file.type.includes('image')) {
                toast('')
                return
            }
            let This = this
            let data = {file}
            let fileInfo = {fieldname:"file",encoding:'fromfile_binary',originalname:data.file.name,
                                mimetype:data.file.type,filename:data.file.name,path:'file-path',
                                size:data.file.size,user_id:localStorage.getItem('user_id'),
                                s_id:localStorage.getItem('s_id'),
                                img_kind:'open',random:Math.random(),
                                data:data.file,img_q:0.5}//,img_q:0.1

            let res = await new Promise((resolve)=>{
                rpc_client.sendImage(fileInfo,function(udata){
                    console.log('sendFile-callback-data:'+JSON.stringify(udata))
                    resolve(udata)
                })
            })

            if(!res || !res.data || !res.data.ret){
                this.$message.success('' +res.data.msg)
                return 
            }
            const img = new Image()
            let img_id = res.data.filename
            img.onload =()=>{
                This.addImage(img_id,img)
            } 

            let params = {user_id:localStorage.getItem('user_id'),s_id:localStorage.getItem('s_id'),
                            filename:img_id,img_kind:'open'}//,img_p:'min200'}
            let item = await imageDb.getDataByKey(img_id)//localStorage.getItem('chatlogo-'+chatInfo.chatlogo)
            if(item && item.data){ 
                img.src = item.data;
            }
            else{
                queryImg('',params).then(async (data)=>{
                    let tmpData ='data:image/png;base64,'+data.data
                    img.src = tmpData
                    imageDb.addData({img_id,data:tmpData })
                })
            }
            // img.src = fileResult

            // const reader = new FileReader()
            // reader.onload = (res) => {
            //     const fileResult = res.target.result
            //     const img = new Image()
            //     img.onload = () => {
            //         this.$store.commit('addComponent', {
            //             component: {
            //                 ...commonAttr,
            //                 id: generateID(),
            //                 component: 'Picture',
            //                 label: '',
            //                 icon: '',
            //                 propValue: {
            //                     url: fileResult,
            //                     flip: {
            //                         horizontal: false,
            //                         vertical: false,
            //                     },
            //                 },
            //                 style: {
            //                     ...commonStyle,
            //                     top: 0,
            //                     left: 0,
            //                     width: img.width,
            //                     height: img.height,
            //                 },
            //             },
            //         })

            //         this.$store.commit('recordSnapshot')

            //         // @change 
            //         $('#input').setAttribute('type', 'text')
            //         $('#input').setAttribute('type', 'file')
            //     }

            //     img.src = fileResult
            // }

            // reader.readAsDataURL(file)
        },

        preview(isScreenshot) 
        {
            this.isScreenshot = isScreenshot
            // this.isShowPreview = true
            // this.$store.commit('setEditMode', 'preview')
            let xitem = {style:deepCopy(this.canvasStyleData),data:deepCopy(this.componentData)}
            localStorage.setItem('canvasData',JSON.stringify(xitem.data ))
            localStorage.setItem('canvasStyle',JSON.stringify(xitem.style ))

            if(top ==self)
            {
                this.$router.push('/view')
            }
            else
            {
                let xmsgObj = {xtype:'pop.preview',xmsg:'from pop.creator',xitem}
                window.top.postMessage(JSON.stringify(xmsgObj), '*')
                this.$message.success('')
            }
        },

        save() {
            localStorage.setItem('canvasData', JSON.stringify(deepCopy(this.componentData)))
            localStorage.setItem('canvasStyle', JSON.stringify(deepCopy(this.canvasStyleData)))
            this.$message.success('')
        },
        logout() {
            // localStorage.removeItem('protocol_config')
            // window.location.reload()

            // this.$message.warning('!')
            let user_id = localStorage.getItem('user_id')
            if(user_id)
            {
                this.$message.success('')
                localStorage.removeItem('user_name')
                localStorage.removeItem('user_id')
                localStorage.removeItem('s_id')
                localStorage.removeItem('phone')
                localStorage.removeItem('logo')
                this.loginStatus = ''
            }else{
                // this.$message.success('')
                this.$router.push('/connect')
            }
        },
        sendXMSG()
        {
            localStorage.setItem('canvasData', JSON.stringify(deepCopy(this.componentData)))
            localStorage.setItem('canvasStyle', JSON.stringify(deepCopy(this.canvasStyleData)))
            let xmsgObj = {xtype:'pop.creator2minicard',xmsg:'from pop.creator'}
            window.top.postMessage(JSON.stringify(xmsgObj), '*')
            this.$message.success('')
        },
        store() {
            localStorage.setItem('canvasData', JSON.stringify(deepCopy(this.componentData)))
            localStorage.setItem('canvasStyle', JSON.stringify(deepCopy(this.canvasStyleData)))

            let date = new Date()
            let timeStr = '' + date.getFullYear() + '-' + (1 + date.getMonth()) + '-' + date.getDate() + ':' + date.toLocaleTimeString()
            // localStorage.setItem('canvasData-' + timeStr, JSON.stringify(this.componentData))
            // localStorage.setItem('canvasStyle-' + timeStr, JSON.stringify(this.canvasStyleData))

            let data = { style: this.canvasStyleData, data: deepCopy((this.componentData)) }
            let content = JSON.stringify(data)
            // var content = data;
            let blob = new Blob([content], { type: '' })
            //  {type: "text/plain;charset=utf-8"}  //txt
            // json
            saveAs(blob, 'store-' + timeStr + '.json') 

            this.$message.success('')
        },
        readStore() {
            const a = document.createElement('input')
            a.setAttribute('type', 'file')
            let This = this
            // console.log('this:' + this.)

            a.addEventListener('change', function selectedFileChanged() {
                console.log('data:' + this.files)
                if (this.files.length == 0) return alert('')
                const reader = new FileReader()
                reader.onload = function fileReadCompleted() {
                    console.log('result:' + reader.result)
                    console.log('text:' + reader.result)
                    let { style, data } = JSON.parse(reader.result)
                    // This.canvasStyleData = style
                    console.log('style:' + JSON.stringify(style),data)
                    if(confirm(''))
                    {
                        for(let i=0;data &&i< data.length;i++)
                        {
                            let item = data[i]
                            item.id = generateID()
                            //
                            This.$store.commit('addComponent', { component:JSON.parse(JSON.stringify(item)) })
                            console.log('addComponent:',item)
                        }
                        This.$message.success('')
                    }
                    else{
                        This.$store.commit('setCanvasStyle', style)
                        This.$store.commit('setComponentData', data)
                        This.$message.success('')
                    }
                }

                reader.readAsText(this.files[0])
            })
            a.click()
            
            //  this.$message.success('')
        }, 

        clearCanvas() {
            this.$store.commit('setCurComponent', { component: null, index: null })
            this.$store.commit('setComponentData', [])
            this.$store.commit('recordSnapshot')
        },

        handlePreviewChange() {
            this.isShowPreview = false
            this.$store.commit('setEditMode', 'edit')
        },
    },
}
</script>

<style lang="scss" scoped>
