<template>
    <div ref="container" class="bg">
        <el-button v-if="!isScreenshot" class="close" @click="close"></el-button>
        <el-button v-else class="close" @click="htmlToImage"></el-button>
        <div class="canvas-container">
            <div
                class="canvas"
                :style="{
                    ...getCanvasStyle(canvasStyleData),
                    width: changeStyleWithScale(canvasStyleData.width) + 'px',
                    height: changeStyleWithScale(canvasStyleData.height) + 'px',
                }"
            >
                <ComponentWrapper
                    v-for="(item, index) in copyData"
                    :key="index"
                    :config="item"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { getStyle, getCanvasStyle } from '@/utils/style'
import { mapState } from 'vuex'
import ComponentWrapper from './ComponentWrapper'
import { changeStyleWithScale } from '@/utils/translate'
import { toPng, getFontEmbedCSS } from 'html-to-image'
// import domtoimage from 'dom-to-image'
import { deepCopy } from '@/utils/utils'
import { onActivated } from 'vue'
// import { Parser , serialize , parseFragment , defaultTreeAdapter }   from "parse5"
let tmpCom = null 
export default {
    components: { ComponentWrapper },
    props: {
        isScreenshot: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            copyData: [],
        }
    },
    computed: mapState([
        'componentData',
        'canvasStyleData',
    ]),
    beforeCreate() {
        console.log('canvasStyleData:' + JSON.stringify(this.canvasStyleData))
    },
    async created() {
        console.log('tmpJson:' + JSON.stringify(deepCopy(this.componentData)))
        this.$set(this, 'copyData', deepCopy(this.componentData))

        // pop.lang
        console.log('this.config:' + JSON.stringify(this.canvasStyleData))
        // eslint-disable-next-line
        this.poplang = new PopRuntime({})
        window.poplang = this.poplang // 
        // this.canvasStyleDate.poplang
        const protocolCache = localStorage.getItem('protocol_config')
        console.log('protocolCache:' + JSON.stringify(protocolCache))

        if (protocolCache) {
            this.poplang.setProtocol(JSON.parse(protocolCache))
            this.isok = 'ok'
        } else {
            this.isok = false
        }
        await this.poplang.runScript(null, this.canvasStyleData.code, true)
        let beforeCreateRet = await this.poplang.op(null, 'beforeCreate')
        console.log('beforeCreateRet:', beforeCreateRet, '$ret:', this.poplang.context.$ret, this.poplang.context)
        
        for (let i = 0; i < this.copyData.length; i++) {
            let item = this.copyData[i]
            item.xdata = { ...this.poplang.context.$ret } // Object.assign({}, this.poplang.context.$ret)
            for (let i = 0; i < this.copyData.length; i++) {
                let item = this.copyData[i]
                item.xdata = { ...this.poplang.context.$ret } // Object.assign({}, this.poplang.context.$ret)
                if (item.poplang) {
                    item.poplang.op(null, 'init')
                    // setTimeout(() => {
                    //     item.poplang.runScript(null, item.code)
                    // }, 100)
                }
            }
        }
        
        this.poplang.op(null, 'created')
    },
    beforeMount() {
        this.poplang.op(null, 'beforeMount')
    },
    async activated() {
        let beforeCreateRet = await this.poplang.op(null, 'beforeCreate')
        console.log('onActivated-beforeCreateRet:', beforeCreateRet, '$ret:', this.poplang.context.$ret, this.poplang.context)
    },
    async mounted() {
        // 
        let This = this
        // setTimeout(async () => {
        let ret = await This.poplang.op(null, 'mounted')
        console.log('ret:' + JSON.stringify(ret))

        let beforeCreateRet = await this.poplang.op(null, 'beforeCreate')
        console.log('onActivated-beforeCreateRet:', beforeCreateRet, '$ret:', this.poplang.context.$ret, this.poplang.context)
        // }, 100)
    },
    beforeDestroy() {
        this.poplang.op(null, 'beforeDestroy')
    },
    destroyed() {
        this.poplang.op(null, 'destroyed')
    },
    methods: {
        getStyle,
        getCanvasStyle,
        changeStyleWithScale,

        close() {
            this.$emit('close')
            this.$router.go(-1)
        },

        async htmlToImage() {
            // let w = changeStyleWithScale(this.canvasStyleData.width) 
            // let h = changeStyleWithScale(this.canvasStyleData.height) 
            // console.log('w:' + w + ' h:' + h)
            let ele = this.$refs.container.querySelector('.canvas')
            const fontEmbedCss = await getFontEmbedCSS(ele)
            console.log('ele-info:' + ele.style.width + ',' + ele.style.height + 'padding:' + ele.style.padding)
            console.log('ele-info:' + ele.style.width + ',' + ele.style.height)
            toPng(ele, { fontEmbedCss })
            .then(dataUrl => {
                // console.log('dataUrl:' + dataUrl)
                const a = document.createElement('a')
                a.setAttribute('download', 'screenshot')
                a.href = dataUrl
                a.click()
            })
            .catch(error => {
                console.error('oops, something went wrong!', error)
            })
            .finally(this.close)
            console.log('ele-info:' + ele.style.width)
        },
    },
}
</script>

<style lang="scss" scoped>
.bg {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: fixed;
    background: rgb(0, 0, 0, .5);
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: auto;
    padding: 20px;

    .canvas-container {
        width: calc(100% - 40px);
        height: calc(100% - 120px);
        overflow: auto;

        .canvas {
            background: #fff;
            position: relative;
            margin: auto;
        }
    }

    .close {
        position: absolute;
        right: 20px;
        top: 20px;
    }
}
</style>
