import Vue from 'vue'
import Router from 'vue-router'
import connect from '../views/connect.vue'
import changeSvrNode from "../views/changeSvrNode.vue"
import home from "../views/Home.vue"

Vue.use(Router)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: home
    },
    {
        path: '/index',
        name: 'Home',
        component: home,
    },
    {
        path: '/connect',
        name: 'connect',
        component: connect
    },
    {
      path: '/changeSvrNode',
      name: 'changeSvrNode',
      component: changeSvrNode //
    },
    {
        path: '/view',
        name: 'View',
        component: () => import('@/views/View2'),
        // component: () => import('@/components/Editor/Preview'),
    },
    {
        path: '/view2',
        name: 'View2',
        component: () => import('@/views/View2'),
    },
]

export default new Router({
    // mode: 'history',
    // base: process.env.BASE_URL,
    routes,
})
