<script>
/* eslint-disable */ 
</script>
<template>
    <div class="box">
      <div @click="refresh" style="position: fixed;right: 0px;margin: 5px;font-size: 15px;"></div>      
      <div class="logo" style="text-align: center; padding-left:15px; margin-top:20px; font-size:24px;">
        <img :src="imgUrl" width="117px" height="85px" style="border:2px solid #e0e0e0;margin-bottom: 18px;"><br/>
        DTNS.network
      </div>
      <div style="text-align: left; padding-left:15px; margin-top:20px; font-size:13px;">{{ deviceInfo }}</div>
      <div style="text-align: left; padding-left:15px; margin-top:20px; font-size:13px;">{{ public_key }}</div>
      <div style="text-align: left; padding-left:15px; margin-top:20px; font-size:13px;">{{ sign }}</div>
      <div style="text-align: left; padding-left:15px; margin-top:20px; font-size:13px;-moz-user-select: text;-webkit-user-select: text;user-select: text;">{{ copyData }}</div>
      <!-- <div style="text-align: left; padding-left:15px; margin-top:20px; font-size:16px;">{{ phones }}1</div> -->
      <div style="text-align: left; padding-left:15px; margin-top:20px; font-size:13px;" v-show="notPhone">
        {{ phone }}
        <div style="text-align:center;width:100%; height:213px;margin:0 auto;background-color:#fff;">
         <img :src="url" alt="" height="165px" width="165px" style="margin-top:24px;" @click="copy"><br/>
         
       </div>
    </div>
      <div style="text-align:center;">
        <el-button type="primary" class="add-btn" @click="connect" style="width:95%; border-radius:5px; font-size:16px; background-color:#15a0e7; margin-top:15px; border:none;">DTNS</el-button><br/>
        <el-button type="primary" @click="ok" style="width:95%; border-radius:5px; font-size:16px; background-color:#15a0e7; margin-top:15px; border:none;" v-show="true"></el-button><br/>
        <el-button   @click="change" style="width:95%; border-radius:5px; font-size:16px;  margin-top:15px; border:none;"></el-button>
      </div>
    </div>
  </template>
  
  <script>
import { resolve } from 'mathjs';
import imgUrl from "../../public/static/images/connect-mini.jpg"// "../../../static/images/connect-mini.jpg"
// const rpc_client = {roomid:'nftlist',success:console.log}
  export default {
    data() {
      return {
        public_key: "",
        private_key: "",
        sign:'',
        keyPair:null,
        deviceInfo:'',
        copyData:'',
        smsUrl:'',
        url:'',
        phones:[],
        phone:'',
        notPhone:false,
        imgUrl:imgUrl
      };
    },
    async mounted() {
        await this.connectInfo()
    },
    async activated(){
      // this.ok()
    },
    methods: {
      async refresh()
      {
        // this.ok('yes')
        //changeSvrNode.vue2023-6-16
        let web3name = rpc_client.roomid
        let sessionInfo = await iSessionDb.getDataByKey('session:'+web3name)
        if(sessionInfo && sessionInfo.data)
        {
          let res = sessionInfo.data
          {
            localStorage.setItem('user_name',res.user_name)
            localStorage.setItem('user_id',res.user_id)
            localStorage.setItem('s_id',res.s_id)
            localStorage.setItem('type',res.type)
            localStorage.setItem('phone',res.phone)
            localStorage.setItem('logo',res.logo)

            this.$message("",1000);

            let new_mywallet = rpc_client.mywallet // await iWalletDb.getDataByKey('mywallet:'+this.changetext)
            console.log('new_mywallet:',new_mywallet)
            if(new_mywallet)
            {
              iWalletDb.deleteDataByKey('g_mywallet')
              iWalletDb.addData({key:'g_mywallet',data:new_mywallet})
              window.g_mywallet = rpc_client.mywallet
            }
            await rpc_client.sleep(30)//500)
            this.$router.push('/index')
          }
        }else{
          this.$message("session",2000);
        }
      },
      async ok(xflag = false){
        let This = this
        if(xflag == 'yes'){
          window.g_mywallet =null// rpc_client.init()
          rpc_client.setWallet(g_mywallet)//
          window.g_mywallet = rpc_client.mywallet//g_mywallet
        }
        else {
          window.g_mywallet = {private_key:this.private_key,public_key:this.public_key,
              web3name:rpc_client.roomid}
          rpc_client.setWallet(g_mywallet)
        }
        console.log('connect-ok() -> xflag',xflag,'g_mywallet:',g_mywallet)
        let flag = true
        // while(flag){
          rpc_client.init()
          //--- 2023-6-16
          let iCnt = 0
          while(iCnt<30 && !rpc_client.pingpong_flag){
            iCnt ++ 
            if(iCnt%10==0) This.$message("",2000);// this.$toast("",800);
            await rpc_client.sleep(100)
          }
          if(iCnt>=30){
            This.$message("",2000);//this.$toast("",2000);
            return 
          }
          // rpc_client.setPeerRefreshCallback(async function(){
            let web3name =  rpc_client.roomid ? rpc_client.roomid : 'dtns'
            let timestamp = parseInt( Date.now()/1000)
            let hash = await key_util.hashVal(web3name+':'+timestamp)
            let sign = await key_util.signMsg(hash,rpc_client.mywallet.private_key)

            let userLoginBySafeDevice = async function(params)
            {
              let url = '/user/device/login'
              return new Promise((resolve)=>{
                rpc_client.send(url,params,null,function(rdata){
                    console.log('rdata:'+JSON.stringify(rdata))
                    resolve(rdata.data)
                })
                setTimeout(()=>reject(null),30000)
              })
            }

            let res =  await userLoginBySafeDevice({web3name,sign,timestamp})//This.$api.network.
            // let res= await g_axios.post('http://www.nftlist.com.cn/user/device/login',{web3name,sign,timestamp})
            //   .then(res => {
            console.log('ok->userLoginBySafeDevice-res:',res);
            res = res && res.data ?res.data:res
                // res = res.data
                if (res.ret) {
                  // alert("");
                  // console.log(res.data.user_id)
                  // console.log(res.data.s_id)
                  // console.log(res);
                //   This.$api.network.startWebSocket();
                  
                //   localStorage.setItem("s_id", res.s_id);
                //   localStorage.setItem("user_id", res.user_id);
                //   localStorage.setItem("userInfo",JSON.stringify(res))
                  console.log('')
                //   This.$message.success("",1000);

                  console.log('[ok]session:'+web3name+' save now:',res)

                  localStorage.setItem('user_name',res.user_name)
                  localStorage.setItem('user_id',res.user_id)
                  localStorage.setItem('s_id',res.s_id)
                  localStorage.setItem('type',res.type)
                  localStorage.setItem('phone',res.phone)
                  localStorage.setItem('logo',res.logo)

                    This.$message({
                    duration: 2000,
                    message: ',!',
                    type: 'success',
                    });
                    // loading.close();
                  
                  iSessionDb.deleteDataByKey('session:'+web3name)
                  iSessionDb.addData({key:'session:'+web3name,data:res}) 
                  iSessionDb.addData({key:'session:'+web3name+':'+Date.now(),data:res})

                  window.g_mywallet = rpc_client.mywallet

                  iWalletDb.deleteDataByKey('g_mywallet')
                  iWalletDb.addData({key:'g_mywallet',data:rpc_client.mywallet})
                  iWalletDb.addData({key:'g_mywallet:'+Date.now(),data:rpc_client.mywallet})

                  // mywallet
                  iWalletDb.deleteDataByKey('mywallet:'+web3name)
                  iWalletDb.addData({key:'mywallet:'+web3name,data:rpc_client.mywallet})
                  iWalletDb.addData({key:'mywallet:'+web3name+':'+Date.now(),data:rpc_client.mywallet})

                //   // console.log(s_id);
                  setTimeout(function(){
                    //This.$router.push("/index");
                    // This.$router.push({name:"index",params:{noCache:true}});
                    This.$router.push('/index')
                  },200)
                  flag = false
                } else {
                  This.$message("DTNS.network",2000);
                }
              // });
          // })
          // await rpc_client.sleep(10000)
        // }
      },
        copy(){
            try{
                navigator.clipboard.writeText(this.copyData);
                console.log('')
                this.$message("",5000);
            }catch(ex){
                console.log('copy-exception:'+ex)
                this.$message("",5000);
            }
        },
      async connectInfo(){
        this.notPhone = device.type!='mobile'
        this.deviceInfo = device.os +'-'+device.type +
                '('+ (Math.floor(new Date().getTime()/1000)).toString(36) +')'
        this.keyPair = eccryptoJS.generateKeyPair()
        this.private_key = bs58.encode( this.keyPair.privateKey)
        this.public_key = bs58.encode( eccryptoJS.getPublicCompressed(this.keyPair.privateKey))
        const msg = eccryptoJS.utf8ToBuffer(this.deviceInfo);
        let hash = await eccryptoJS.sha256(msg)
        this.sign = bs58.encode( await eccryptoJS.sign(this.keyPair.privateKey,hash,true))

        let splitStr = '|'
        this.copyData = splitStr+this.sign+splitStr
            +this.public_key.substring(this.public_key.length-4,this.public_key.length)
            +splitStr+this.deviceInfo+splitStr+rpc_client.roomid+splitStr
        console.log('copyData:'+this.copyData)

        this.phones = null;
        if(typeof g_axios !='undefined' && rpc_client.web3appInfo && rpc_client.web3appInfo.network_info 
          && rpc_client.web3appInfo.network_info.phones_urls && rpc_client.web3appInfo.network_info.phones_urls.length>0)
        {
          let phonesUrl =  rpc_client.web3appInfo.network_info.phones_urls[parseInt(Math.random()*1599999)%rpc_client.web3appInfo.network_info.phones_urls.length]
          let phonesRet = await g_axios.get(phonesUrl)
          this.phones = phonesRet && phonesRet.data ?phonesRet.data :phonesRet
          console.log('phonesUrl:',phonesUrl,phonesRet)
        }else{
          this.phones =  await g_axios.get(typeof g_phones_default_url !='undefined' ? g_phones_default_url : 'https://static.yunapi.org/phones.json')
        }
        // this.phones =  await g_axios.get('https://static.yunapi.org/phones.json')//this.$api.network.queryPopSafeSmsPhones()
        console.log('phones:',this.phones)//,this.phones.data)
        this.phones = this.phones && this.phones.data ? this.phones.data : this.phones
        if(!this.phones || this.phones.length<=0 )
        {
            this.$message("connect-url",5000);
            return 
        }

        this.phone = this.phones[parseInt(Math.floor( Math.random()*199999999))%this.phones.length]
        let url = 'sms:'+this.phone+'?'+this.copyData
        console.log('connect-url:'+url)
        this.smsUrl= url

        let This = this
        QRCode.toDataURL(this.copyData)
            .then(url => {
            //console.log(url)
            This.url = url
            })
            .catch(err => {
                console.error(err)
            })
      },
      change(){
        this.$router.push("/changeSvrNode");
        // this.$router.push('/login')
      },
      async connect() {
        if(!this.phones || this.phones.length<=0 )
        {
            this.$message("connect-url",5000);
            return 
        }
        
        var a = document.createElement("a")//a
        a.setAttribute("href",this.smsUrl)//
        a.setAttribute("target","_blank")
        let clickEvent = document.createEvent("MouseEvents");
        clickEvent.initEvent("click", true, true);
        a.dispatchEvent(clickEvent);
      }
    }
  };
  </script>
  <!-- lang="stylus"  -->
  <style scoped>
  .box {
    width:100%;
    height:100%;
    background-color:#fff;
    position:fixed;
  }
  .box .van-nav-bar__text{
    color:#000;
    font-size:15px;
  }
  
  * {
    touch-action: pan-y;
  }
  
  .textarea  .van-cell {
    width: 350px;
    margin:30px auto;
  }
  
  .submit .van-button {
    display: block;
    margin: 0 auto;
    background-color: #15a0e7;
    border:none;
  
  }
  .logo {
    text-align:center;
    font-size:20px;
  }
  .info .van-cell{
    background-color:#f5f5f5;
  }
  </style>
  